import React from 'react';
import './SideMenu.scss';
import { NavLink } from 'react-router-dom';

const SideMenu = ({title, paths}) => {
  return (
    <div className='sideMenu'>
      <nav className='sideMenu__links border-gradient'>
        <h4 className='sideMenu__title'>{title}</h4>
        {paths.map((path) => 
          <NavLink key={path?.pathLink}
            to={path?.pathLink} 
            className={({ isActive }) => (isActive ? `sideMenu__link sideMenu__link-active ${path?.className ? path?.className : ''}` 
                                                    : `sideMenu__link ${path?.className ? path?.className : ''}` )}
            onClick={path?.onClick}
          >
            {path.icon && 
              <div className='link__icon'>
                <img src={path.icon} alt={`${path.icon}`} />
              </div>
            }
            <p className={`link__title ${path?.className ? path?.className : ''}`}>{path.pathName}</p>
          </NavLink>
        )}
      </nav>
    </div>
  );
};

export default SideMenu;