import React, { useRef, useEffect} from 'react';
import { TRANSFERS_SEND_ROUTE, TRANSFERS_TOPUP_ROUTE, TRANSFERS_WITHDRAW_ROUTE, OWERVIEW_ROUTE, TRANSFERS_ROUTE, MOBILE, UI_MOBILE } from './../../utils/consts';
import topup from '../../img/transfers/topup.svg';
import send from '../../img/transfers/send.svg';
import withdraw from '../../img/transfers/withdraw.svg';

import LinkBack from '../../components/LinkBack/LinkBack';
import SideMenu from './../../components/SideMenu/SideMenu';
import { Outlet, useLocation, useNavigate } from 'react-router-dom';
import './Transfers.scss';
import Promo from './../../components/Promo/Promo';
import UI from '../../store/UI';
import { observer } from 'mobx-react-lite';

const Transfers = observer(() => {
  const { pathname } = useLocation();
  const sideMenu = useRef(null);
  const navigate = useNavigate();

  useEffect(() => {
    if(UI.device === UI_MOBILE) {
      if(pathname === TRANSFERS_ROUTE) {
        sideMenu?.current.classList.remove('hidden');
        sideMenu?.current.classList.add('visible');
      }
      else {
        sideMenu?.current.classList.remove('visible');
        sideMenu?.current.classList.add('hidden');
      }
    } else {
      sideMenu?.current.classList.remove('hidden');
      sideMenu?.current.classList.add('visible');
      if(pathname === TRANSFERS_ROUTE) {
        navigate(TRANSFERS_TOPUP_ROUTE);
      }
    }
  }, [pathname, UI.device]);

  return (
    <div className='transfers'>
      <div className='transfers__menu' ref={sideMenu}>
        <LinkBack path={OWERVIEW_ROUTE} title='Transfers and Payments' />
        <SideMenu 
          title='Transfers' 
          paths={[
            {pathName: 'Top up card', pathLink: TRANSFERS_TOPUP_ROUTE, icon: topup},
            {pathName: 'Send money', pathLink: TRANSFERS_SEND_ROUTE, icon: send},
            {pathName: 'Withdraw', pathLink: TRANSFERS_WITHDRAW_ROUTE, icon: withdraw}
          ]}
        />
      </div>
      <div className='transfers__operation'>
        <Outlet />
      </div>
      <Promo  title='Read the latest news on the system update'/>
    </div>
  );
});

export default Transfers;